.page {
  font-family: "Inter", sans-serif;
  @include mainTextStyle;

  scroll-behavior: smooth;

  &:has(&__menu:target) {
    overflow: hidden;
  }
  &__body {
    margin: 0;
    min-width: 320px;
  }
  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1;

    opacity: 0;
    transition: all $effectDuration;
    transform: translateX(-100%);
    pointer-events: none;
    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}
